import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  username:string;
  password:string;
  success:boolean;
  invalid:boolean;
  error:boolean;
  loading:boolean;

  constructor(
    private authService: AuthService
  ) { }

  ngOnInit() {
    this.loading = false;
    this.success = false;
    this.invalid = false;
    this.error = false;
  }

  register() {
    this.loading = true;

    this.authService.checkUser(this.username, this.password).subscribe((validcheck) => {
      if (validcheck.valid) {
        this.authService.register(this.username, this.password).subscribe((response) => {
          if (response.user != null) {
            this.success = true;
            this.invalid = false;
            this.error = false;
            this.loading = false;
          }
          else {
            this.error = true;
            this.invalid = false;
            this.success= false;
            this.loading = false;
          }
        });
      }
      else {
        this.invalid = true;
        this.success = false;
        this.error = false;
        this.loading = false;
      }
    });

  }

}



