import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';

import 'rxjs/add/operator/map';

interface user {
  token: string;
}

interface response {
  success: boolean;
  user: user;
}

interface validCheck {
  valid: boolean;
}

@Injectable()
export class AuthService {
  token: string;
  username: string;
  password: string;
  session: boolean;

  constructor(
    public http:HttpClient
  ) {
  }

  register(username: string, password: string) {
    var dataObject = {
      user: {
        username: username,
        password: password
      }
    }

    return this.http.post<response>('https://synergism.azurewebsites.net/api/auth/register', dataObject);
  }

  login(username: string, password: string) {
    var dataObject = {
      user: {
        username: username,
        password: password
      }
    }
    return this.http.post<response>('https://synergism.azurewebsites.net/api/auth/login', dataObject);
  }

  checkUser(username: string, password: string) {
    var dataObject = {
      user: {
        username: username,
        password: password
      }
    }
    return this.http.post<validCheck>('https://synergism.azurewebsites.net/api/auth/checkUser', dataObject);
  }


}



