import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';
import { DataService } from '../../services/data.service';

interface user {
  token: string;
}

interface response {
  success: boolean;
  user: user;
}

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  username: string;
  password: string;
  loading: boolean;
  failed: boolean;
  incorrect: boolean;

  constructor(
    private authService: AuthService,
    private router: Router,
    private dataService: DataService
  ) { }

  ngOnInit() {
  }

  login() {
    this.loading = true;
    this.authService.username = this.username;
    this.authService.password = this.password;
    this.dataService.getGradebook().subscribe(
      res => {
        this.dataService.gradebook = res;
        if (this.dataService.gradebook.course.length == 0) {
          this.incorrect = true;
          this.loading = false;
        }
        else {
          this.authService.session = true
          this.dataService.checkEmpty();
          console.log(this.dataService.gradebook);
          this.dataService.calculateGPA();
          this.dataService.setRecents();
          this.router.navigateByUrl('/home');
        }
      },
      err => {
        this.failed = true;
        this.loading = false;
      }

    )
  }
}



