import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';
import { DataService } from '../../services/data.service';
import { Chart } from 'chart.js';

interface newCategory {
  weightedpct: String,
  weight: String,
  pointspossible: String,
  points: String,
  name: String;
}

interface newAssignment {
  type: String;
  score: score;
  name: String;
  date: String;
}

interface score {
  pointspossible: number;
  points: number;
}

interface course {
  summary: newCategory[];
  name: String;
  grade: String;
  assignments: newAssignment[];
}

interface gradebook {
  course: course[];
}


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  overview: gradebook;
  loading: boolean;
  gpa: number;
  weightedgpa: number;
  canvas: any;
  chart: any;

  constructor(
    private authService: AuthService,
    private router: Router,
    private dataService: DataService
  ) { }

  ngOnInit() {
    if (!this.authService.session) {
      this.router.navigateByUrl('/login');
    }
    this.overview = this.dataService.gradebook;
    this.gpa = this.dataService.gpa;
    this.weightedgpa = this.dataService.weightedgpa;

    //var data = this.dataService.getDataPoints(0);

    //this.chart = new Chart('canvas', {
    //  type:'line',
    //  data: data,
    //  options: {
    //    scales: {
    //        xAxes: [{
    //            type: 'time'
    //        }]
  //      }
  //  }
//    });
  }


  logout() {
    this.authService.username = null;
    this.authService.password = null;
    this.authService.session = false;
  }


}



